main {
    // min-height: calc(100vh - 8.5rem);
    // padding: 0 1rem;
}

.main-container{
    max-height: 400px;
    overflow-y: scroll;
}



//   I phone 5
@media screen and (max-height: 570px) {
    .main-container {
        // width: 390px;
        max-height: 363px;
    }
}

//   I phone 6-8 
@media screen and (max-height: 670px) and (min-height: 570px){
    .main-container {
        // width: 390px;
        max-height: 450px;
    }
  }

//   I phone 6-8 plus
@media screen and (min-height: 730px) {
    .main-container {
        max-height: 453px;
        margin-top: 1rem;
    }
  }
//   I phone x
@media screen and (min-height: 810px) {
    .main-container {
        // width: 390px;
        max-height: 542px;
        margin-top: 12px;
    }
  }


  //   Ipad
  @media screen and (min-height: 1000px) {
    .main-container {
        // width: 390px;
        max-height: 810px;
    }
  }


