.time-slot-lists-container{
    // max-height: 340px;
    .loaderContainer{
        overflow-y: hidden;
        height: 200px;
        margin-top: 20px;
    }
}
// MATERIAL UI CLASSES
.MuiDialogActions-root{
    justify-content: space-between!important;
}

.date-picker-title{
    font-size: 15px;
    margin-top: 1rem;
}

.picker-red-title{
  color: #680505;
  font-size: 15px;
  margin-top: 1rem;
}

.header-calendar-app{
    padding: 5px 20px;
    background-color: #273650;
    color: white;
    width: 100%;
    
}
.date-range-in-calendar{
    width: 60%;
    font-size: 15px;
    font-family: DancingScript;  
    color: white;
    font-size: 14px;
    margin-right: 10px;
}

.calendar-icon-in-calendar{
    color: white;
    margin-right: 10px;
}

.weekly-dates-container-calendar-app{
    position: absolute;
    top: 0;
    right: 0;
}

//   I phone 5
@media screen and (max-height: 570px) {
    .time-slot-lists-container{
        height: 310px;
    }
  }

//   I phone 6-8 
@media screen and (min-height: 571px)  and (max-height: 670px) {
    .time-slot-lists-container{
            height: 400px;
  }
}
//   I phone 6-8 plus 
@media screen and (min-height: 730px) and (max-height: 810px) {
    .time-slot-lists-container{
        height: 470px;
    }
  }

//   I phone x
@media screen  and (min-height: 811px)  and (max-height: 1000px){
    .time-slot-lists-container{
        height: 570px;
    }
  }

  //   Ipad
@media screen and (min-height: 1024px) {
    .time-slot-lists-container{
        height: 770px;
    }
  }